era relación laboral ni obliga a la organización a pagar prestaciones sociales. La duración es igualmente en común acuerdo dependiendo del trabajo a realizar. Aquí es importante mencionar que el empleado recibe un sueldo al cual se le descuenta únicamente por concepto de retención en la fuente.")])]),_c('div',{staticClass:"col-lg-5",attrs:{"data-aos":"zoom-in"}},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"350px"},attrs:{"src":require("@/assets/curso/tema1/t1_15.png"),"alt":"contratista independiente"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-3"},[_c('h4',[_vm._v("Este contrato se podrá efectuar cuando se cumplan las siguientes premisas: ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-4"},[_c('div',{staticClass:"col-lg-7"},[_c('div',{staticClass:"cp mb-4"},[_c('div',{staticClass:"dots"}),_c('p',{staticClass:"mb-0"},[_vm._v("Por otra parte, es importante mencionar que el contratista, sin importar la duración o el valor del contrato, deberá estar afiliado y ser cotizante obligatorio al Sistema General de la Seguridad Social en Salud y Pensiones. ")])]),_c('div',{staticClass:"crd crd--purple"},[_c('p',{staticClass:"mb-0"},[_vm._v("Según el "),_c('strong',{staticClass:"txt--purple"},[_vm._v("Artículo 18 de la Ley 1122 de 2007")]),_vm._v(" define la base de cotización de estos subsistemas de la siguiente forma: establece que será del 40% de los ingresos mensuales recibidos por el contratista, que en ningún caso podrá ser inferior a un salario mínimo legal vigente, ni superior a 25 salarios mínimos legales vigentes. El "),_c('strong',{staticClass:"txt--purple"},[_vm._v("Artículo 23 del Decreto 1703 de 2002")]),_vm._v(", establece que en los contratos de vigencia indeterminada el ingreso base de cotización será equivalente al 40% del valor bruto facturado en forma mensualizada,